import { Component, Vue } from 'vue-property-decorator';
import PsoCharacter from '@/models/PsoCharacter';
import store from "@/store/store";
import PsoCharacterView from '@/views/PsoCharacterPlanner/PsoCharacterView/PsoCharacterView.vue';

@Component({
    components: {
        PsoCharacterView
    }
})
export default class PsoCharacterPlanner extends Vue {
    selectedCharacter?: PsoCharacter | null = null;

    get PsoCharacters(): PsoCharacter[] {
        return this.$store.state.psoCharacters;

    }

    AddCharacter() {
        this.$store.dispatch("pleaseAddPsoCharacter");
    }

    mounted() {
        this.$root.$emit('set-info', {
            title: "PSO Character Planner",
            info:
                `A character planner for Phantasy Star Online 1. 
While WIP Should help you keep track of materials usage.

This uses your localStorage(Google it) to keep this data when you leave, so clearing your broswer data will blow this out.

I play on GameCube so if there are inconsistencies, it's because old data old, and I only play on GameCube.

Most data I use is from www.pso-world.com

- To get started, click new character.
- Select the new character and click on the edit icon to the right of the character name.
- Set up your character, and click save at the bottom.
- As you update your character the data is saved in localStorage, so you can leave and come back later!

Planned updates
- Mag planner/tracker per character
- Automatic Section ID calculation
- Better Character Portraits
`
        });
    }
}