import {Component, Vue, Prop } from 'vue-property-decorator';
import PsoCharacter from '@/models/PsoCharacter';
import {PsoProfessions, PsoSectionIds} from "@/enums/PsoEnums"
import store from "@/store/store";
import {clamp } from "@/utils/math";

@Component
export default class PsoCharacterView extends Vue {
    @Prop ({required: true})
    characterId!: number;

    professions: any = PsoProfessions;
    sectionIds: any = PsoSectionIds;

    inputName: string = "";
    inputLevel: number = 0;
    inputPower: number = 0;
    inputDefense: number = 0;
    inputEvasion: number = 0;
    inputMind: number = 0;
    inputLuck: number = 0;
    inputProfession: PsoProfessions = PsoProfessions.HUMAR;
    inputSectionId: PsoSectionIds = PsoSectionIds.BLUEFULL;

    selectedCharacter: PsoCharacter = new PsoCharacter();
    editing: boolean = false;

    get temporaryRemaining(){
        return this.temporaryMixedMatGoal - (
            Number(this.inputPower) +
            Number(this.inputDefense) +
            Number(this.inputEvasion) +
            Number(this.inputMind) +
            Number(this.inputLuck)
        );
    }

    get temporaryHpMatGoal(): number{
        return this.selectedCharacter.hpMatGoal;
    }
    get temporaryTpMatGoal(): number {
        return this.selectedCharacter.getTpMatGoalForProfession(this.inputProfession);
    }
    get temporaryMixedMatGoal(): number {
        return this.selectedCharacter.getMixedMatAllowedForProfession(this.inputProfession);
    }

    SaveClickHandler(){
        this.UpdateCharacter();
        this.editing = false;
        this.$store.dispatch("pleaseStorePsoCharacters");
    }

    EditClickHandler(){
        this.UpdateInputs();
        this.editing = true;
    }

    UpdateInputs(){
        this.inputName = this.selectedCharacter.name;
        this.inputLevel = this.selectedCharacter.level;
        this.inputPower = this.selectedCharacter.powMatGoal;
        this.inputDefense = this.selectedCharacter.defMatGoal;
        this.inputEvasion = this.selectedCharacter.evpMatGoal;
        this.inputMind = this.selectedCharacter.mndMatGoal;
        this.inputLuck = this.selectedCharacter.luckMatGoal;
        this.inputProfession = this.selectedCharacter.profession;
        this.inputSectionId = this.selectedCharacter.sectionId;
    }

    UpdateCharacter(){
        this.selectedCharacter.name = this.inputName;
        this.selectedCharacter.level = Number(this.inputLevel);
        this.selectedCharacter.powMatGoal = Number(this.inputPower);
        this.selectedCharacter.defMatGoal = Number(this.inputDefense);
        this.selectedCharacter.evpMatGoal = Number(this.inputEvasion);
        this.selectedCharacter.mndMatGoal = Number(this.inputMind);
        this.selectedCharacter.luckMatGoal = Number(this.inputLuck);
        this.selectedCharacter.profession = this.inputProfession;
        this.selectedCharacter.sectionId = this.inputSectionId;
    }

    incrementLevel() {
        this.selectedCharacter.level = clamp(this.selectedCharacter.level + 1, 1, 200);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    decrementLevel() {
        this.selectedCharacter.level = clamp(this.selectedCharacter.level - 1, 1, 200);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }

    incrementHpMatTotal(){
        this.selectedCharacter.hpMatTotal = clamp(this.selectedCharacter.hpMatTotal + 1, 0, this.selectedCharacter.hpMatGoal);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    decrementHpMatTotal(){
        this.selectedCharacter.hpMatTotal = clamp(this.selectedCharacter.hpMatTotal - 1, 0, this.selectedCharacter.hpMatGoal);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    incrementTpMatTotal(){
        this.selectedCharacter.tpMatTotal = clamp(this.selectedCharacter.tpMatTotal + 1, 0, this.selectedCharacter.tpMatGoal);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    decrementTpMatTotal(){
        this.selectedCharacter.tpMatTotal = clamp(this.selectedCharacter.tpMatTotal - 1, 0, this.selectedCharacter.tpMatGoal);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    
    incrementPowMatTotal(){
        if(this.selectedCharacter.mixedMatUnused > 0){
            this.selectedCharacter.powMatTotal = this.selectedCharacter.powMatTotal + 1;
            this.$store.dispatch("pleaseStorePsoCharacters");
        }
    }
    decrementPowMatTotal(){
        this.selectedCharacter.powMatTotal = clamp(this.selectedCharacter.powMatTotal - 1, 0, 250);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    incrementDefMatTotal(){
        if(this.selectedCharacter.mixedMatUnused > 0){
            this.selectedCharacter.defMatTotal = this.selectedCharacter.defMatTotal + 1;
            this.$store.dispatch("pleaseStorePsoCharacters");
        }
    }
    decrementDefMatTotal(){
        this.selectedCharacter.defMatTotal = clamp(this.selectedCharacter.defMatTotal - 1, 0, 250);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    incrementEvpMatTotal(){
        if(this.selectedCharacter.mixedMatUnused > 0){
            this.selectedCharacter.evpMatTotal = this.selectedCharacter.evpMatTotal + 1;
            this.$store.dispatch("pleaseStorePsoCharacters");
        }
    }
    decrementEvpMatTotal(){
        this.selectedCharacter.evpMatTotal = clamp(this.selectedCharacter.evpMatTotal - 1, 0, 250);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    incrementMndMatTotal(){
        if(this.selectedCharacter.mixedMatUnused > 0){
            this.selectedCharacter.mndMatTotal = this.selectedCharacter.mndMatTotal + 1;
            this.$store.dispatch("pleaseStorePsoCharacters");
        }
    }
    decrementMndMatTotal(){
        this.selectedCharacter.mndMatTotal = clamp(this.selectedCharacter.mndMatTotal - 1, 0, 250);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }
    incrementLuckMatTotal(){
        if(this.selectedCharacter.mixedMatUnused > 0){
            this.selectedCharacter.luckMatTotal = this.selectedCharacter.luckMatTotal + 1;
            this.$store.dispatch("pleaseStorePsoCharacters");
        }
    }
    decrementLuckMatTotal(){
        this.selectedCharacter.luckMatTotal = clamp(this.selectedCharacter.luckMatTotal - 1, 0, 250);
        this.$store.dispatch("pleaseStorePsoCharacters");
    }

    mounted(){
        const characters = this.$store.state.psoCharacters;
        const thisCharacter = characters.filter((char:PsoCharacter) => {return char.id === this.characterId })[0];
        this.selectedCharacter = thisCharacter;
        this.UpdateInputs();
    }
}